import keyLayout from '../json/keys.json';
import audioKey from './../sound/key.mp3';

class CreateKeys {
  constructor(keyObj) {
    this.keyObj = keyObj;
  }

  elements = {
    keysContainer: null,
    keys: [],
    input: {},
  };

  properties = {
    capsLock: false,
    keyName: 'small',
    shift: false,
    alt: false,
  };

  init() {
    this.elements.input = document.querySelector('.keyboard-input');

    this.elements.main = document.createElement('div');
    this.elements.keysContainer = document.createElement('div');

    this.elements.keysContainer.classList.add('keyboard');
    this.elements.keysContainer.appendChild(this.#createKeys(CreateKeys.#getLocalLang()));
    this.elements.keys = this.elements.keysContainer.querySelectorAll('.keyboard__key');

    this.#setKeysTextContent();

    this.#setListener();

    document.body.appendChild(this.elements.keysContainer);
  }

  #createKeys() {
    const fragment = document.createDocumentFragment();

    for (const key in this.keyObj) {
      const keyElement = document.createElement('button');
      keyElement.setAttribute('type', 'button');

      switch (this.keyObj[key].width) {
        case 'wide':
          keyElement.classList.add('keyboard__key', 'keyboard__key--wide', `${this.keyObj[key].code}`);
          break;

        case 'extra-wide':
          keyElement.classList.add('keyboard__key', 'keyboard__key--extra-wide', `${this.keyObj[key].code}`);
          break;

        default:
          keyElement.classList.add('keyboard__key', `${this.keyObj[key].code}`);
          break;
      }

      fragment.appendChild(keyElement);
    }

    return fragment;
  }

  #setKeysTextContent(keyName = this.properties.keyName, keyLang = CreateKeys.#getLocalLang()) {
    for (let i = 0; i < this.elements.keys.length; i++) {
      if (keyLang === 'En') {
        if (this.keyObj[i][`${keyName}En`]) {
          if (this.properties.capsLock && this.properties.shift) {
            if (this.keyObj[i].capsLockEn) {
              this.elements.keys[i].textContent = this.keyObj[i].smallEn;
            } else this.elements.keys[i].textContent = this.keyObj[i][`${keyName}En`];
          } else if (this.properties.capsLock && this.keyObj[i].capsLockEn) {
            this.elements.keys[i].textContent = this.keyObj[i][`${keyName}En`];
          } else if (this.properties.shift) {
            this.elements.keys[i].textContent = this.keyObj[i][`${keyName}En`];
          } else this.elements.keys[i].textContent = this.keyObj[i].smallEn;
        } else this.elements.keys[i].textContent = this.keyObj[i].smallEn;
      }

      if (keyLang === 'Ru') {
        if (this.keyObj[i][`${keyName}Ru`]) {
          if (this.properties.capsLock && this.properties.shift) {
            if (this.keyObj[i].capsLockRu) {
              this.elements.keys[i].textContent = this.keyObj[i].smallRu;
            } else this.elements.keys[i].textContent = this.keyObj[i][`${keyName}Ru`];
          } else if (this.properties.capsLock && this.keyObj[i].capsLockRu) {
            this.elements.keys[i].textContent = this.keyObj[i][`${keyName}Ru`];
          } else if (this.properties.shift) {
            this.elements.keys[i].textContent = this.keyObj[i][`${keyName}Ru`];
          } else if (this.keyObj[i].smallRu) {
            this.elements.keys[i].textContent = this.keyObj[i].smallRu;
          } else this.elements.keys[i].textContent = this.keyObj[i].smallEn;
        } else this.elements.keys[i].textContent = this.keyObj[i].smallEn;
      }
    }
  }

  #setListener() {
    for (let i = 0; i < this.elements.keys.length; i++) {
      this.elements.keys[i].addEventListener('click', () => {
        this.#getListener(this.keyObj[i].code);
      });
    }
  }

  #getListener(keyCode) {
    const { input } = this.elements;
    const start = input.selectionStart;
    const end = input.selectionEnd;

    for (let i = 0; i < this.elements.keys.length; i++) {
      if (this.keyObj[i].code === keyCode) {
        switch (this.keyObj[i].code) {
          case 'Backspace':
            input.value = input.value.substring(0, start - 1) + input.value.substring(end);
            input.selectionEnd = start === end ? end - 1 : end;
            break;

          case 'Tab':
            input.value = `${input.value.substring(0, start)}\t${input.value.substring(end)}`;
            input.selectionEnd = start === end ? end + 1 : end;
            break;

          case 'Delete':
            input.value = input.value.substring(0, start) + input.value.substring(start + 1);
            input.selectionEnd = start === end ? end : end;
            break;

          case 'CapsLock':
            this.#capsLockKey();
            input.selectionEnd = start === end ? end : end;
            break;

          case 'Enter':
            input.value = `${input.value.substring(0, start)}\n${input.value.substring(end)}`;
            input.selectionEnd = start === end ? end + 1 : end;
            break;

          case 'ShiftLeft':
            this.#shiftKey();
            this.#changeLang();
            input.selectionEnd = start === end ? end : end;
            break;

          case 'ShiftRight':
            this.#shiftKey();
            this.#changeLang();
            input.selectionEnd = start === end ? end : end;
            break;

          case 'ControlLeft':
            input.selectionEnd = start === end ? end : end;
            break;

          case 'ControlRight':
            input.selectionEnd = start === end ? end : end;
            break;

          case 'LWin':
            input.selectionEnd = start === end ? end : end;
            break;

          case 'AltLeft':
            (!this.properties.alt) ? this.properties.alt = true : this.properties.alt = false;
            this.#changeLang();
            input.selectionEnd = start === end ? end : end;
            break;

          case 'AltRight':
            (!this.properties.alt) ? this.properties.alt = true : this.properties.alt = false;
            this.#changeLang();
            input.selectionEnd = start === end ? end : end;
            break;

          case 'Space':
            input.value = `${input.value.substring(0, start)} ${input.value.substring(end)}`;
            input.selectionEnd = start === end ? end + 1 : end;
            break;

          case 'ContextMenu':
            input.selectionEnd = start === end ? end + 1 : end;
            break;

          case 'ArrowLeft':
            input.selectionEnd = start === end ? end - 1 : end;
            break;

          case 'ArrowRight':
            input.selectionStart = start === end ? start + 1 : end;
            break;

          default:
            input.value = input.value.substring(0, start) + this.elements.keys[i].textContent + input.value.substring(end);
            input.selectionEnd = start === end ? end + this.elements.keys[i].textContent.length : end;
            if (this.properties.shift && this.properties.capsLock) {
              this.properties.shift = true;
              this.#shiftKey();
              this.#setKeysTextContent('shift');
            } else if (this.properties.shift) this.#shiftKey();

            break;
        }

        input.focus();
        const audio = new Audio(audioKey);
        audio.play();
      }
    }
  }

  #capsLockKey() {
    if (!this.properties.capsLock) {
      document.querySelector('.CapsLock').classList.toggle('active-hold');
      this.properties.capsLock = true;
      this.properties.keyName = 'shift';
      this.#setKeysTextContent('shift');
    } else {
      document.querySelector('.CapsLock').classList.toggle('active-hold');
      this.properties.capsLock = false;
      this.properties.keyName = 'small';
      this.#setKeysTextContent('small');
    }
  }

  #shiftKey() {
    if (!this.properties.shift) {
      document.querySelector('.ShiftLeft').classList.toggle('active-hold');
      document.querySelector('.ShiftRight').classList.toggle('active-hold');
      this.properties.shift = true;
      this.properties.keyName = 'shift';
      this.#setKeysTextContent('shift');
    } else {
      document.querySelector('.ShiftLeft').classList.toggle('active-hold');
      document.querySelector('.ShiftRight').classList.toggle('active-hold');
      this.properties.shift = false;
      this.properties.keyName = 'small';
      this.#setKeysTextContent('small');
    }
  }

  #changeLang() {
    if (this.properties.alt && this.properties.shift) {
      if (CreateKeys.#getLocalLang() === 'En') {
        CreateKeys.#setLocalLang('Russian');
        this.#setKeysTextContent();
        this.properties.alt = false;
        if (!this.properties.capsLock) this.#shiftKey();
      } else {
        CreateKeys.#setLocalLang('English');
        this.#setKeysTextContent();
        this.properties.alt = false;
        if (!this.properties.capsLock) this.#shiftKey();
      }
    }
  }

  static #setLocalLang(language) {
    localStorage.setItem('Language', language);
  }

  static #getLocalLang() {
    if (!localStorage.getItem('Language')) {
      CreateKeys.#setLocalLang('English');
    }
    return localStorage.getItem('Language').substring(0, 2);
  }
}

export const createKeyboard = new CreateKeys(keyLayout);
