import createPreloader from './assets/scripts/preloader';
import {createKeyboard} from './assets/scripts/createKeyboard';

import './style.scss';
import './assets/styles/normalize.css';

createPreloader();

const textArea = document.createElement('textarea');
textArea.className = 'keyboard-input';
document.body.appendChild(textArea);

const text = document.createElement('p');
text.textContent = 'Switch language: Shift + Alt';
text.className = 'text';
document.body.appendChild(text);

window.addEventListener('DOMContentLoaded', createKeyboard.init());

document.addEventListener('keydown', (e) => {
  e.preventDefault();
  document.querySelector(`.${e.code}`).click();
  document.querySelector(`.${e.code}`).classList.toggle('active');
});

document.addEventListener('keyup', (e) => {
  e.preventDefault();
  if (document.querySelector(`.${e.code}`).classList.contains('active')) {
    document.querySelector(`.${e.code}`).classList.remove('active');
  }
});
