export default function createPreloader() {
  const preloader = document.createElement('div');
  preloader.className = 'preloader';
  preloader.style.cssText = `
    position: fixed ;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    background: black;
    z-index: 1001;
    `;
  document.body.prepend(preloader);

  const preloaderImage = document.createElement('div');
  preloaderImage.className = 'preloader-image';
  preloaderImage.style.cssText = `
    position: fixed;
    display: inline-block;
    overflow: hidden;
    top: calc(50vh - 325px);
    left: calc(50vw - 450px);
    width: 900px;
    height: 650px;
    background-size: 900px 650px;
    `;
  preloader.append(preloaderImage);

  window.onload = function onLoader() {
    preloader.removeChild(preloaderImage);
    document.body.removeChild(preloader);
  };
}
